<template>
  <div class="demoBox">
    <scan ref="qrcode" @ok="getResult" @err="geterror"></scan>
  </div>
</template>

<script>
import scan from "@/components/scanComp/scan";
export default {
  components: {
    scan,
  },
  methods: {
    getResult(result) {
      // console.log('result', result)
      let res = JSON.parse(result);
      if (result) {
        this.$message.success(JSON.stringify(result));
        // uni.navigateTo({
        //     url: '/pages/sys/workbench/deviceCode/deviceCode?id=' + res.id
        // });
        // location.reload();
        // // 或者
        // // this.$refs.qrcode.stop()
      } else {
        this.$message.error("未识别到设备码");
      }
    },
    geterror(e) {
      console.log("e", e);
      this.$message.error(e);
      setTimeout(() => {
        this.$router.go(-1);
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
.demoBox {
  height: 100%;
  width: 100%;
}
</style>
